* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  height: 100vh;
  background-color: #ffffcd;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  font-weight: bold;
  display: flex;
}

.card {
  width: 600px;
  aspect-ratio: 1;
  background-image: url("https://images.unsplash.com/photo-1517423568366-8b83523034fd?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8M3x8aGlwc3RlcnxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=500&q=60");
  background-position: 0;
  background-size: cover;
  padding: 54px 44px;
  overflow: hidden;
}

h1 {
  text-transform: uppercase;
  color: #141414;
  text-align: center;
  background-color: #ffffcde6;
  border-radius: 20px;
  margin-bottom: 20px;
  padding: 10px;
}

.text {
  width: -moz-fit-content;
  width: fit-content;
  background-color: #ffffffb3;
  border-radius: 20px;
  margin-bottom: 6px;
  padding: 10px;
  font-size: 18px;
  transform: translate(calc(-100% - 44px));
}

.first {
  transition: all .3s .1s;
}

.second {
  transition: all .3s .2s;
}

.third {
  transition: all .3s .3s;
}

.card:hover .text {
  transform: translate(0);
}

